/* CameraPreview.css */

body .pre-join-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 150px;
    background-color: #a2b0e1 !important ;
    background-repeat: repeat-y !important; /* main.css'teki stilin üzerine yazmak için !important kullanıldı */
    color: white !important; /* main.css'teki renk stilini geçersiz kılar */
}

body .pre-join-container button {
    margin-top: 10px;
    background-color: #1B2653 !important; /* main.css'teki buton rengini geçersiz kılar */
    color: #fff !important; /* main.css'teki buton yazı rengini geçersiz kılar */
    border: none !important; /* main.css'teki olası kenarlık stilini geçersiz kılar */
    padding: 12px 24px; /* Buton için daha iyi bir görsel düzen sağlamak amacıyla padding eklendi */
    border-radius: 8px; /* Butonun kenarlarını yuvarlayarak daha modern bir görünüm kazandırıldı */
    cursor: pointer;
    transition: background-color 0.3s ease; /* Hover durumunda renk geçişini sağlamak için */
}

body .pre-join-container button:hover {
    background-color: #334D92 !important; /* Hover durumunda farklı bir arka plan rengi uygulandı */
}

/* Camera Preview Container */
.camera-preview {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    background-color: #202124;
    color: white;
}

.controls {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px; /* Dropdown ile buton arasında boşluk */
    margin-bottom: 20px; /* Kontrollerin altına boşluk */
}

.dropdown {
    padding: 10px;
    border-radius: 8px;
    border: 1px solid #ccc;
    background-color: #1B2653;
    color: white;
    font-size: 16px;
    cursor: pointer;
}

.camera-button {
    padding: 10px 20px;
    border-radius: 8px;
    border: none;
    background-color: #1B2653;
    color: white;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.camera-button:hover {
    background-color: #334D92;
}


/* Audio Settings Container */
.audio-settings {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0;
    padding: 10px;
    background-color: #1B2653;
    border-radius: 8px;
    color: white;
}

/* Background Options Container */
.background-options {
    display: flex;
    justify-content: space-around;
    margin: 20px 0;
    padding: 10px;
    background-color: #333;
    border-radius: 8px;
    color: white;
}

/* Join Control Container */
.join-control {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 20px;
}

.join-control button {
    padding: 10px 20px;
    background-color: #1B2653;
    color: #fff;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.join-control button:hover {
    background-color: #334D92;
}

